html {
	box-sizing: border-box;
}
*,
*::after,
*::before {
	box-sizing: inherit;
}
/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}
/*        (ul) */
ul,li {
	list-style: none;
	padding-left: 0;
	margin: 0;
}
/*        */
a {
	text-decoration: none;
	color: inherit;
}
/*      */
img {
	display: block;
	max-width: 100%;
	height: auto;
}
/*      address */
address {
	font-style: normal;
}